.Input {
  margin-bottom: 10px;
  max-width: 480px; }

.field {
  border-radius: 40px;
  padding: 10px 20px;
  background-color: #fff;
  border: 1px solid transparent;
  color: #323232;
  font-family: "ProximaNovaLight", sans-serif;
  font-size: 16px;
  width: 100%;
  resize: none; }
  .field:focus {
    border-color: #28bbf0; }

.border {
  border-color: #cccccc; }

.error {
  border-color: #ee4634; }

.errorMessage {
  color: #ee4634; }



