.container {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  padding-top: 20px;
  padding-bottom: 60px;
  position: relative; }

.caption {
  text-align: center;
  color: #ffffff; }

.title {
  font-size: 21px;
  margin-bottom: 20px;
  font-family: "ProximaNovaSemibold", sans-serif; }

.description {
  font-family: "ProximaNovaLight", sans-serif;
  max-width: 700px;
  margin: 0 auto; }

.links {
  position: absolute;
  right: 0;
  top: 20px; }
  @media screen and (max-width: 550px) {
    .links {
      position: static; } }

@media screen and (max-width: 550px) {
  .btnHome {
    position: absolute;
    left: 5px;
    bottom: 0; } }

@media screen and (max-width: 550px) {
  .btnHome div {
    left: 0;
    -ms-flex-pack: end;
        justify-content: flex-end; }
    .btnHome div p {
      -ms-flex-order: -1;
          order: -1;
      margin-left: 0;
      margin-right: 10px; } }

.btnHome:hover div {
  width: 150px; }

@media screen and (max-width: 550px) {
  .btnEdit {
    position: absolute;
    right: 5px;
    bottom: 0; } }

.btnEdit:hover div {
  width: 170px; }



