.bg {
  padding: 10% 0; }
  @media screen and (max-width: 768px) {
    .bg {
      padding: 20px 0; } }

.container {
  max-width: 680px;
  margin: 0 auto; }

.close {
  position: absolute;
  top: -40px;
  right: 0;
  cursor: pointer; }
  .close svg {
    fill: #cccccc;
    -webkit-transition: all .5s ease;
    -o-transition: all .5s ease;
    transition: all .5s ease; }
  .close:hover svg {
    fill: #323232; }

.arrowIcon {
  fill: #cccccc;
  -webkit-transition: all .5s ease;
  -o-transition: all .5s ease;
  transition: all .5s ease; }

.arrowLeft {
  top: 20%;
  left: -30px; }
  @media screen and (max-width: 768px) {
    .arrowLeft {
      display: none; } }
  .arrowLeft:before {
    display: none; }
  .arrowLeft:hover .arrowIcon {
    fill: #323232; }

.arrowRight {
  top: 20%;
  right: -30px; }
  @media screen and (max-width: 768px) {
    .arrowRight {
      display: none; } }
  .arrowRight:hover .arrowIcon {
    fill: #323232; }
  .arrowRight:before {
    display: none; }



