.AlbumList {
  background: #f6f6f6;
  padding: 60px 0; }

.container {
  max-width: 1180px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px; }
  @media screen and (max-width: 768px) {
    .container {
      padding: 0 20px; } }

.head {
  position: relative;
  margin-bottom: 20px; }
  .head h2 {
    margin-bottom: 0; }

.addAlbum {
  position: absolute;
  right: 5px;
  top: 10px;
  -webkit-box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.25); }
  @media screen and (max-width: 550px) {
    .addAlbum {
      top: 0; } }
  .addAlbum:hover div {
    width: 140px; }

.list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px; }

.itemEnter {
  opacity: 0;
  -webkit-transition: all .4s ease;
  -o-transition: all .4s ease;
  transition: all .4s ease; }

.itemEnterActive {
  opacity: 1; }

.itemExit {
  opacity: 1;
  -webkit-transition: all .4s ease;
  -o-transition: all .4s ease;
  transition: all .4s ease; }

.itemExitActive {
  opacity: 0; }



